"use client";

import React, { useState } from "react";
import { useRouter } from "next/navigation";
import { motion } from "framer-motion";
import { ShieldCheck, Anchor } from "lucide-react";
import { loginUser } from "./actions";

export default function Home() {
  const [officialId, setOfficialId] = useState("");
  const [name, setName] = useState("");
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      const result = await loginUser(officialId, name);
      if (result.success) {
        // Backup in localStorage for client-side easy access
        localStorage.setItem("user", JSON.stringify({ officialId, name, id: result.userId }));
        router.push("/modules");
      } else {
        console.error(result.error);
        setLoading(false);
      }
    } catch (err) {
      console.error("Login failed", err);
      setLoading(false);
    }
  };

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-6 relative overflow-hidden bg-navy-900">

      {/* Background Decor */}
      <div className="absolute top-0 left-0 w-full h-full opacity-10 pointer-events-none">
        <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-steel rounded-full blur-[128px]"></div>
        <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-navy-700 rounded-full blur-[128px]"></div>
      </div>

      <motion.div
        className="z-10 w-full max-w-md p-8 bg-navy-800/50 backdrop-blur-xl border border-steel-200/10 rounded-2xl shadow-2xl"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8, delay: 2.6 }} // Delay to match curtain reveal
      >
        <div className="flex flex-col items-center mb-8">
          <motion.div
            initial={{ scale: 0 }}
            animate={{ scale: 1 }}
            transition={{ type: "spring", stiffness: 260, damping: 20, delay: 2.8 }}
            className="p-4 bg-navy-900 rounded-full border-2 border-gold-600 mb-4 shadow-lg shadow-gold-600/20"
          >
            <Anchor className="w-10 h-10 text-gold-500" />
          </motion.div>
          <h1 className="text-3xl font-bold text-gold-500 font-heading tracking-wider">LOGIN</h1>
          <p className="text-steel-200 text-sm mt-2">Enter your details to proceed</p>
        </div>

        <form onSubmit={handleLogin} className="space-y-6">
          <div className="space-y-2">
            <label htmlFor="officialId" className="block text-xs font-medium text-gold-400 uppercase tracking-wide">
              Official ID
            </label>
            <input
              id="officialId"
              type="text"
              required
              value={officialId}
              onChange={(e) => setOfficialId(e.target.value)}
              className="w-full px-4 py-3 bg-navy-900 border border-navy-600 rounded-lg text-steel-100 placeholder-navy-600 focus:outline-none focus:border-gold-500 focus:ring-1 focus:ring-gold-500 transition-all"
              placeholder="e.g. N12345"
            />
          </div>

          <div className="space-y-2">
            <label htmlFor="name" className="block text-xs font-medium text-gold-400 uppercase tracking-wide">
              Name
            </label>
            <input
              id="name"
              type="text"
              required
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="w-full px-4 py-3 bg-navy-900 border border-navy-600 rounded-lg text-steel-100 placeholder-navy-600 focus:outline-none focus:border-gold-500 focus:ring-1 focus:ring-gold-500 transition-all"
              placeholder="e.g. A.B. PERERA"
            />
          </div>

          <motion.button
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
            type="submit"
            disabled={loading}
            className="w-full py-3.5 bg-gradient-to-r from-gold-600 to-gold-500 text-navy-900 font-bold rounded-lg shadow-lg hover:shadow-gold-500/20 transition-all disabled:opacity-50 disabled:cursor-not-allowed uppercase tracking-wider text-sm flex items-center justify-center gap-2"
          >
            {loading ? "Processing..." : "Enter System"}
            {!loading && <ShieldCheck className="w-4 h-4" />}
          </motion.button>
        </form>
      </motion.div>
    </main>
  );
}
